:root {
}

*::before,
*::after,
* {
	box-sizing: border-box;
}

body,
html {
	padding: 0%;
	margin: 0%;
	height: 100%;
	font-size: 10px;
	background-color: rgb(244, 244, 244);
	color: #333;
}

p {
	font-size: 1.6rem;
}

small {
	font-size: 1.2rem;
}

.container {
	margin: auto;
	max-width: 1000px;
	padding: 4em;
}

.title {
	text-align: center;
	font-size: 4.4rem;
}

.form {
	display: grid;
}

.label {
	font-size: 1.2rem;
	margin-bottom: 0.2em;
	text-transform: uppercase;
}

.input {
	font-size: 1.6rem;
	padding: 0.5rem 2rem;
	line-height: 2.8rem;
	border-radius: 20px;
	border: 1px solid #ddd;
	margin-bottom: 1em;
}

.button {
	background-color: rgba(0, 0, 0, 0.75);
	color: white;
	padding: 1rem 2rem;
	border: 1px solid rgba(0, 0, 0, 0.75);
	border-radius: 20px;
	font-size: 1.4rem;
	cursor: pointer;
	transition: background-color 250ms;
}

.button:hover {
	background-color: rgba(0, 0, 0, 0.85);
}

.card {
	padding: 2rem 4rem;
	border-radius: 10px;
	box-shadow: 1px 1px 5px rgba(0, 0, 0, 0.25);
	margin-top: 1.5em;
	background-color: white;
}

.card-title {
	margin-bottom: 0.5rem;
	font-size: 3.2rem;
	margin-top: 0.2rem;
}

.card-image {
	margin: auto;
	display: block;
}

@media (min-width: 786px) {
	.form {
		grid-template-columns: auto 1fr auto;
		gap: 1em;
		align-items: center;
	}

	.label {
		grid-column: span 1;
	}

	.input {
		margin-bottom: 0px;
		grid-column: span 1;
	}

	.button {
		grid-column: span 1;
	}
}
